import { Link } from "react-router-dom";
import CIBLogo from "./resources/logo.png";

const content = [
  "Home",
  "About",
  "Leadership",
  "Network",
  "The Fund",
  "Education",
  "Join Us",
  "Coffee Chats",
];

export const Nav = ({ active }) => {
  return (
    <div className="absolute top-0 left-0 margin-auto w-full">
      <div className="flex justify-between items-center flex-shrink-0 text-white pt-4 pb-3 pl-8 pr-48 font-poppins">
        <Link to="/">
          <img src={CIBLogo} alt="CIB" className="h-16" />
        </Link>
        {content.map((item, index) => {
          if (item === "Home") {
            return (
              <Link to={`/`}>
                <span
                  className={`font-extralight text-md ${
                    active === item.toLowerCase().replace(" ", "-")
                      ? "underline"
                      : ""
                  }`}
                  style={{
                    textUnderlineOffset: "5px",
                    textDecorationThickness: "1px",
                  }}
                >
                  {item}
                </span>
              </Link>
            );
          }
          return (
            <Link to={`/${item.toLowerCase().replace(" ", "-")}`} key={index}>
              <span
                className={`font-extralight text-md ${
                  active === item.toLowerCase().replace(" ", "-")
                    ? "underline"
                    : ""
                }`}
                style={{
                  textUnderlineOffset: "5px",
                  textDecorationThickness: "1px",
                }}
              >
                {item}
              </span>
            </Link>
          );
        })}
      </div>
    </div>
  );
};
